<template>
    <div class="error-container">
      <div class="e-404">
        <div class="error-img"></div>
        <h1>您查找的页面不存在！</h1>
      </div>
    </div>
</template>

<script>
export default {
  name: 'Error404'
}
</script>

<style lang="scss" scoped>
@import '@/assets/style/variables.scss';

.error-container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  .e-404{
    .error-img {
      width: 527px;
      height: 300px;
      margin: 0 auto 70px;
      background-repeat: no-repeat;
      background-size: contain;
    }
    h1 {
      font-size: 30px;
      font-weight: normal;
    }
  }
  .e-404 .error-img {
    background-image: url('./img/404.png');
  }
}
</style>



